.verify {
    min-height: 60vh;
    display: grid;
}

.spinner {
    width: 100px;
    height: 100px;
    place-self: center;
    border: 5px solid honeydew;
    border-top-color: red;
    border-radius: 50%;
    animation: rotate 1s infinite;
}

@keyframes rotate {
    100% {
        transform:rotate(360deg);
    }
}